.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-repeat: no-repeat;
  background-position: center;

  @include hover(transform, scale(1.2));

  &--menu {
    background-image: url("../images/menu/menu.png");

    @include onDesktop {
      display: none;
    }
  }

  &--close {
    background-image: url("../images/menu/cross.png");
  }
}
